@import "../../global.scss";

.footer {
  height: 100%;
  background-color: var(--background-color-dark);
  scroll-snap-align: start;

  display: flex;

  overflow: unset;

  .footerContainer {
    width: 100%;
    height: 100%;
    background: linear-gradient(var(--background-color-dark), var(--background-color-light));

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    position: relative;

    .footerTitle {
      position: absolute;
      top: 3px;
      left: 10px;

      font-size: 2.5em;
      z-index: 100;
    }



    .internalLinksContainer {
      width: 100%;
      height: calc(50% + 3 * var(--height-transition-triangle));
      padding: 0 10%;
      position: absolute;
      top: 0;
      left: 0;
      z-index: 0;

      display: flex;

      &.inView {

      }

      @include for-mobile {
        padding-right: 5%;
        padding-top: 70px;
      }

      .internalLinkContainer {
        height: 100%;
        width: 100%;
        position: relative;
        top: 0;

        display: flex;
        flex-direction: column;
        justify-content: flex-start;

        .internalLink {
          width: fit-content;

          display: flex;
          flex-direction: column;
          justify-content: flex-start;

          .linkText {
            width: fit-content;

            display: flex;
            flex-direction: row;
            align-items: flex-start;

            h1 {
              @include for-mobile {
                font-size: 4em;
              }
            }
          }
        }

        #educationLink {
          padding-top: 200px;
        }
      }

    }

    .externalLinksContainer {
      width: 100%;
      height: calc(50% + 2 * var(--height-transition-triangle));
      position: absolute;
      bottom: 0;
      left: 0;
      z-index: 1;
      padding: 0 10%;

      overflow: hidden;

      background-color: black;

      transition: all 2s ease-in-out;
      clip-path: polygon(0% 100%, 50% calc(100% - var(--height-transition-triangle)), 100% 100%, 100% 100%, 0% 100%);

      display: flex;
      justify-content: center;

      &.inView {
        animation: revealExternalLinks 1s ease-in-out both 1s;
      }

      @keyframes revealExternalLinks {
        0% {
          clip-path: polygon(0% 100%, 50% calc(100% - var(--height-transition-triangle)), 100% 100%, 100% 100%, 0% 100%);
        }
        40% {
          clip-path: polygon(0% calc(3 * var(--height-transition-triangle)), 50% calc(2 * var(--height-transition-triangle)), 100% calc(3 * var(--height-transition-triangle)), 100% 100%, 0% 100%);
        }
        60% {
          clip-path: polygon(0% calc(3 * var(--height-transition-triangle)), 50% calc(2 * var(--height-transition-triangle)), 100% calc(3 * var(--height-transition-triangle)), 100% 100%, 0% 100%);
        }
        100% {
          clip-path: polygon(0% calc(4 * var(--height-transition-triangle)), 50% calc(2 * var(--height-transition-triangle)), 100% 0%, 100% 100%, 0% 100%);
        }
      }

      @include for-mobile {
        padding: 0 5%;
        flex-direction: column;
      }

      .externalLinkContainer {
        height: 100%;
        width: 100%;
        position: relative;
        bottom: 0;

        display: flex;
        flex-direction: column;
        justify-content: flex-end;

        @include for-mobile {
          flex-direction: row;
          justify-content: initial;
        }

        .externalLink {
          width: fit-content;

          display: flex;
          flex-direction: column;
          justify-content: flex-end;

          @include for-mobile {
            flex-direction: row;
            align-items: flex-end;
          }

          .linkText {
            width: fit-content;

            display: flex;
            flex-direction: row;
            align-items: flex-start;

            stroke: var(--text-color-top);

            h1 {
              @include for-mobile {
                font-size: 4em;
              }
            }

            &:hover {
              stroke: var(--text-color-bottom);
            }

            #externalLink {
              height: 16px;
              width: 16px;
            }
          }
        }

        #githubLink {
          @include for-mobile {

          }
        }

        #codepenLink {
          padding-bottom: 100px;
        }
      }

      #githubLinkContainer {
        justify-content: flex-end;
      }
    }
  }
}