@import "../../global.scss";

.skills {
  height: calc(100% + var(--height-transition-triangle) * 0.5);
  background-color: var(--background-color-dark);
  scroll-snap-align: start;

  position: relative;

  overflow: unset;

  .skillsContainer {
    width: 100%;
    height: 100%;
    background: linear-gradient(var(--background-color-dark), var(--background-color-light));

    display: flex;
    align-items: center;
    justify-content: center;

    position: relative;

    .skillsTitle {
      position: absolute;
      top: 3px;
      left: 10px;

      font-size: 2.5em;
      z-index: 100;
    }

    .listsContainer {
      width: 80%;
      padding: 0;

      display: grid;
      grid-template-columns: 1fr 1fr 1fr 1fr;

      @include for-mobile {
        width: 100%;
        height: 100%;
        padding: 70px 5% 50px 0;

        grid-template-rows: 1fr 1fr;
        grid-template-columns: 1fr 1fr;
      }

      .listContainer {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-start;
        overflow: hidden;

        @include for-mobile {
          justify-content: flex-start;
        }

        .listTitle {
          overflow: hidden;

          @include for-mobile {
            font-size: 3.5em;
          }
        }

        ul {
          overflow: hidden;

          li {

            .skill {
              @include for-mobile {
                font-size: 1.5em;
              }
            }
          }
        }
      }
    }
  }

  &:after {
    content: "";
    width: 100%;
    height: var(--height-transition-triangle);
    display: block;
    position: relative;
    left: 0;
    bottom: calc(var(--height-transition-triangle));
    background-color: var(--background-color-dark);
    -webkit-clip-path: polygon(0 0, 50% 100%, 100% 0, 100% 100%, 0 100%);
    clip-path: polygon(0 0, 50% 100%, 100% 0, 100% 100%, 0 100%);
    z-index: 99;
  }
}