@import "./../../global.scss";

.testPageTwo {
  width: 100%;
  height: 100%;
  background: linear-gradient(var(--background-color-light), var(--background-color-dark));
  color: var(--text-color-bottom);

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  overflow: hidden;

  .imageContainer {
    width: 50%;
    position: relative;
    z-index: 1;

    @include for-mobile {
      width: 150%;
    }

    .dancersImage {
      max-width: 100%;
      position: relative;
      top: 0;
      left: 0;
    }

    .upperLineCue {
      width: 35px;
      height: 35px;
      position: absolute;
      top: 22.2%;
      left: 25.4%;
      border-radius: 50%;
      background-color: #8f1552;
      animation: cueAnimation ease-in-out;
      animation-duration: 1s;
      animation-iteration-count: 1;
      animation-fill-mode: both;
      animation-delay: 500ms;
    }

    #cueTwo {
      top: 14.2%;
      left: 36.4%;
      animation-delay: 2200ms;
      animation-duration: 1s;
    }

    #cueThree {
      top: 10.8%;
      left: 46.4%;
      animation-delay: 2500ms;
      animation-duration: 1s;
    }

    #cueFour {
      top: 10.8%;
      left: 46.4%;
      animation-delay: 2500ms;
      animation-duration: 1s;
    }

    .lowerLineCue {
      display: none;
      width: 35px;
      height: 35px;
      position: absolute;
      top: 44.6%;
      left: 29.3%;
      border-radius: 50%;
      background-color: #8f1552;
      //animation: cueAnimation 1s ease-in-out infinite alternate;
    }

    @keyframes cueAnimation {
      0% {
        opacity: 1;
        transform: scale(0);
      }
      50% {
        opacity: 1;
        transform: scale(0.6);
      }
      100% {
        opacity: 0;
        transform: scale(1.2);
      }
    }

    .linesForDancersImage {
      max-width: 100%;
      position: absolute;
      top: 0;
      left: 0;

      clip-path: polygon(0% 0%, 10% 0%, 10% 35%, 10% 45%, 10% 100%, 0% 100%);

      animation: revealLines 9s ease-in-out;
      animation-iteration-count: 1;
      animation-fill-mode: both;
      animation-delay: 500ms;
      @keyframes revealLines {
        0% {
          clip-path: polygon(0% 0%, 10% 0%, 10% 35%, 10% 45%, 10% 100%, 0% 100%);
        }
        50% {
          clip-path: polygon(0% 0%, 100% 0%, 100% 35%, 10% 45%, 10% 100%, 0% 100%);
        }

        100% {
          clip-path: polygon(0% 0%, 100% 0%, 100% 35%, 100% 45%, 100% 100%, 0% 100%);
        }
      }
    }

    .lineForDancers {
      position: absolute;
      top: 0;
      left: 0;

      path {
        stroke-dasharray: 40%;
        stroke-dashoffset: 40%;
        animation: drawLine 5s ease-in-out infinite alternate;

        @keyframes drawLine {
          from {
            stroke-dashoffset: 40%;
          }
          to {
            stroke-dashoffset: 0;
          }
        }
      }
    }
  }

  .textContainer {
    position: relative;
    transform: translateY(-450px);
    padding-bottom: 50px;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    animation: slideInText 2s ease-in-out;
    animation-fill-mode: both;
    animation-delay: 5s;
    z-index: 0;

    @include for-mobile {
      width: 90%;
      text-align: center;
    }

    @keyframes slideInText {
      from {
        opacity: 0;
        transform: translateY(-450px);
      }
      to {
        opacity: 1;
        transform: translateY(0px);
      }
    }

    .headlineBig {
      font-family: var(--font-family-big-heading);
      font-size: var(--font-size-big-heading);
      font-weight: var(--font-weight-big-heading);
      background: var(--background-big-heading);
      background-clip: text;
      -webkit-background-clip: text;
      color: transparent;
    }

    #projectName {
      margin: 0;

      @include for-mobile {
        font-size: 3.5em;
      }
    }

    .text {
      font-family: var(--font-family-paragraph);
      font-size: var(--font-size-paragraph);
      font-weight: var(--font-weight-paragraph);
      background: var(--background-paragraph);
      background-clip: text;
      -webkit-background-clip: text;
      color: transparent;
    }

    #projectDescription {
      @include for-mobile {
        font-size: 1.5em;
      }
    }

    a {
      height: 25px;
      font-family: var(--font-family-paragraph);
      font-size: var(--font-size-paragraph);
      font-weight: var(--font-weight-paragraph);
    }
  }
}